.account-page {
  font-family: Arial, sans-serif;
  color: #ffffff;
  max-width: 400px;
  margin: 0 auto;
  padding: 30px;
}

.account-heading {
  margin-left: 10px;
  margin-top: 75px;
  text-align: center;
}

.account-button {
  padding: 12px 24px;
  border: none;
  cursor: pointer;
  font-size: 1em;
  background: linear-gradient(to right, #faa805, #d47f00);
  color: white;
  border-radius: 5px;
  transition: background-color 0.3s, -webkit-transform 0.3s;
  transition: background-color 0.3s, transform 0.3s;
  transition: background-color 0.3s, transform 0.3s, -webkit-transform 0.3s;
  margin-top: 10px;
  margin-bottom: 20px;
  display: block;
  width: 100%;
  box-sizing: border-box;
  font-weight: 700;
}

.account-button:hover {
  background: linear-gradient(to right, #d47f00, #faa805);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.account-input {
  padding: 12px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: calc(100% - 24px);
  background-color: white;
  color: black;
  font-size: 1em;
}

.form-label {
  display: block;
  margin-top: 20px;
  margin-bottom: 5px;
}

.feedback-button {
  box-shadow: 0 0 20px #faa805;
  color: black;
}

@media (max-width: 768px) {
  .account-button:hover {
    background: linear-gradient(to right, #faa805, #d47f00);
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
}
