}

.comments-list {
  list-style-type: none;
  padding: 0;
}

.comments-list > div {
  border: 1px solid #444; /* Darker border for contrast */
  padding: 10px;
  margin-top: 10px;
  background-color: #222; /* Dark gray background for comments */
  position: relative;
}

.comments-list button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 5px;
  font-size: 14px;
