  flex-grow: 1;
  padding: 10px;
  margin-right: 10px;
  border: 1px solid #444; /* Darker border */
  background-color: #222; /* Dark gray input background */
  color: #fff; /* White text color */
}

.comment-input button {
  padding: 10px 15px;
  background-color: #555; /* Dark gray button color */
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background-color 0.2s;
}

.comment-input button:hover {
  background-color: #777; /* Lighter gray on hover */
}

.comments-list .comment-depth-1 {
  padding-left: 20px;
  border-left: 3px solid #444; /* Darker border for nested comments */
}

.comment-depth-0 {
  /* No padding for top-level comments */
}

