.profiles-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 5%;
    gap: 5%;
    height: 100%;
}

.profile {
    width: 140px; /* Set a fixed width */
    height: 140px; /* Set a fixed height */
    padding: 4px;
    background-color: #faa805;
    display: flex;
    align-items: center; /* Center vertically */
    justify-content: center; /* Center horizontally */
}

.prof-pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.likes-container {
    height: 100%;
}

@media (max-width: 768px) {
    .profiles-container {
        padding: 20px;
    }
}

.messages-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    height: calc(100vh - 108px);
    padding-top: 20px;
    background-color: #111;
    position: relative;
}

.chat-messages {
    flex: 1 1;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    scroll-behavior: auto;
}

.chat-input {
    background-color: #111;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 2px solid #faa805;
    border-radius: 0 0 5px 5px;
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
}

.chat-input input {
    flex: 1 1;
    padding: 5px;
    border: none;
    border-radius: 10px;
    color: black;
}

.chat-input button {
    background-color: #faa805;
    color: #111;
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    cursor: pointer;
    margin-left: 10px;
}

.message {
    max-width: 70%;
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    font-weight: 400;
}

.sent {
    align-self: flex-end;
    background-color: #FAA805;
    color: #fff;
}

.received {
    align-self: flex-start;
    background-color:  #2a2a2a;
    color: white;
}

.back-btn{
    background: none;
    color: #FAA805;
    border: none;
    outline: none;
    position: absolute;
    top: 8px;
    left: 10px;
    font-size: 18px;
    visibility: hidden;
}

.info-btn {
    background: none;
    color: #FAA805;
    border: none;
    outline: none;
    position: absolute;
    top: 8px;
    right: 10px;
    font-size: 18px;
    visibility: hidden;
}

.msgs-name {
    margin: 0;
    margin-bottom: 8px;
    text-align: center;
}

.msgs-header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: #111; /* Adjust the background color as needed */
    z-index: 100; /* Adjust the z-index as needed */
}

@media (max-width: 768px) {
    .messages-container {
        padding-top: 0;
        justify-content: unset;
        width: 100%;
    }

    .back-btn,
    .info-btn {
        visibility: visible;
    }
}
.profile-container {
    display: flex;
    flex-direction: column;
    max-height: calc(100vh - 80px);
    overflow-y: scroll;
    background-color: #111;
    color: #fff;
    align-items: center;
}

.basic-info {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    border: 2px solid #faa805;
}

.basic-info p {
    font-size: 1.05rem;
}

.no-lynks {
    text-align: center;
}

.profile-info {
    width: 100%;
}

.info-carousel-container {
    width: 100%;
    height: 388px;
    overflow: hidden;
    margin: 0 auto;
    position: relative;
}

.info-profile-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.info-profile-image-box {
    /* width: 388px; */
    height: 388px;
}

.info-carousel .carousel-status {
    position: absolute;
    top: 0;
    right: 5px;
    font-size: 14px;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.9);
    color: #fff;
}

.loading-indicator {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.chats-matches-container {
    display: flex;
    grid-gap: 2.5%;
    gap: 2.5%;
    background-color: #111;
}

.matches-column {
    flex: 0 0 25%;
}

.messaging-column {
    flex: 0 0 40%;
}

.info-column {
    flex: 0 0 30%;
    background-color: #111;
    overflow-y: scroll;
    height: calc(100vh - 80px);
}

.loading-gif {
    width: 100%;
}

@media (max-width: 768px) {
    .chats-matches-container {
        display: block;
    }
    /* .messaging-column,
    .info-column {
        visibility: hidden;
        display: none;
        margin: 0;
        padding: 0;
    } */
}
.container {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #111;
    color: white;
    margin: 0;
    width: 100%;
    height: calc(100vh - 95px);
    display: flex;
    justify-content: center;
    align-items: center;
}

.password-reset-container {
    background: white;
    color: black;
    padding: 3em;
    border-radius: 8px;
    width: 350px;
    box-shadow: 0px 10px 25px -5px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.pass-reset-title {
    color: black;
    margin: 0;
    margin-bottom: 10px;
}

.inputBox {
    width: calc(100% - 20px); /* Adjusted width to account for padding */
    padding: 12px 10px;
    margin-bottom: 1.5em;
    border: 1px solid #faa805;
    border-radius: 5px;
    transition: border 0.2s;
}

.inputBox:focus {
    border: 1px solid #faa805;
    outline: none;
}

.login {
    width: 100%;
    background: linear-gradient(to right, #faa805, #d47f00);
    color: white;
    border: none;
    padding: 12px 0;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s, -webkit-transform 0.2s;
    transition: background-color 0.3s, transform 0.2s;
    transition: background-color 0.3s, transform 0.2s, -webkit-transform 0.2s;
    font-size: 1em;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center;
}

.login:hover {
    background: linear-gradient(to right, #d47f00, #faa805);
    -webkit-transform: none;
            transform: none;
}

.error-message {
    color: red;
    border: 2px solid red;
    padding: 8px;
}

.success-message {
    color: green;
}

.auth-label {
    color: #faa805;
    text-align: left;
    width: 100%;
    display: block;
    margin-bottom: 5px;
}

.forgot {
    color: blue;
    margin-bottom: 1.5em;
}

.current {
    margin-bottom: 3px;
}

@media (max-width: 768px) {
    .password-reset-container {
        width: 277px;
    }
}

/* MyAccount.css */
.my-account-page {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  grid-gap: 5%;
  gap: 5%;
  padding: 40px;
  overflow-y: scroll;
  height: calc(100vh - 214px);
}

.account-button {
  padding: 12px 24px;
  border: none;
  cursor: pointer;
  font-size: 1em;
  background: linear-gradient(to right, #faa805, #d47f00);
  color: white;
  border-radius: 5px;
  transition: background-color 0.3s, -webkit-transform 0.3s;
  transition: background-color 0.3s, transform 0.3s;
  transition: background-color 0.3s, transform 0.3s, -webkit-transform 0.3s;
  margin-top: 10px;
  margin-bottom: 20px;
  display: block;
  width: 100%;
  box-sizing: border-box;
}

.account-button:hover {
  background: linear-gradient(to right, #d47f00, #faa805);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.account-input {
  padding: 12px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: calc(100% - 24px);
  background-color: white;
  color: black;
  font-size: 1em;
}

.profile-section {
  margin-top: 30px;
}

.image-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* 2 columns */
  grid-gap: 16px;
  gap: 16px; /* Adjust the gap as needed */
}

.image-box {
  width: 100%;
  aspect-ratio: 1/1; /* Maintain a 1:1 aspect ratio for each box */
  overflow: hidden;
  border: 1px solid #faa805;
  position: relative;
}

.image-box img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Maintain image aspect ratio and cover the box */
}

.profile-pics{
  flex: 0 0 47.5%;
  height: 100%;
}

.title-name {
  text-align: center;
}

.editing-input {
  background-color: black;
  border: 1px solid #faa805;
  color: white;
  width: 100%;
  padding: 10px;
  border-radius: 10px;
  box-sizing: border-box;
}

.editing-input:focus {
  border: 1px solid gold;
  outline: none;
}

.field-box {
  width: 100%;
}

.data-pencil {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border: 1px solid white;
  padding: 10px;
  border-radius: 10px;
}

.save-btn {
  background-color: #faa805;
  border: none;
  padding: 12px 24px;
  cursor: pointer;
  font-size: 1em;
  color: white;
  border-radius: 5px;
}

.profile-fields {
  flex: 0 0 47.5%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  right: 0;
}

.profile-field {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 600;
}

.field-label {
  color: grey;
}

/* Add this to your existing CSS or create a new CSS file */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999; /* Ensure it appears above other elements */
}

.popup-content {
  background: #fff; /* Popup background color */
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  position: relative;
}

.popup-text {
  color: black;
  font-size: 20px;
  font-weight: 600;
}

.popup-btn {
  background-color: #faa805;
  color: black;
  padding: 7px 15px;
  border: 1px solid black;
  border-radius: 5px;
  margin: 10px;
}

.close-popup {
  position: absolute;
  top: 5px;
  right: 5px;
  background: none;
  border: none;
  outline: none;
  color: black;
  font-weight: 900;
}

.delete-button {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 50%;
  padding: 5px 8px;
  cursor: pointer;
}

.delete-button .x-button {
  color: white;
  font-size: 25px;
}

.add-button {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
  border: 4px solid white;
  border-radius: 50%;
  color: white;
  font-size: 25px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 10px;
}

@media (max-width: 768px) {
  .my-account-page {
    flex-direction: column;
    height: calc(100vh - 150px);
    display: block;
  }

  .profile-fields {
    position: relative;
    height: unset;
    margin-top: 40px;
  }

  .profile-pics {
    height: unset;
  }
}
